<template>
  <div class="sponsor" id="sponsor">
    <h2 class="slogan">Sponsor😀</h2>
    <div class="sponsor-list">
        <div class="sponsor-item">
            <h3>Friendship sponsor</h3>
            <div class="list">
                <a href="javascript:void(0);">
                    <img src="../assets/new_logo.min.png" alt="">
                </a>
            </div>
        </div>
        <div class="sponsor-item">
            <h3>Platinum sponsor</h3>
            <div class="list">
                <a href="javascript:void(0);">
                    <img src="../assets/readme-blue.png" alt="">
                </a>
            </div>
            <div class="list">
                <a href="javascript:void(0);">
                    <img src="../assets/qordoba.svg" alt="">
                </a>
            </div>
        </div>
    </div>
    <div class="sponsor-detail">
        <h3>Sponsor Mark Text Development</h3>
        <p>Mark Text is an MIT licensed open source project,you will always be able to download the latest version from <a href="#">GitHub release page</a>. Mark Text is still in development, and its development is inseparable from all sponsors. I hope you join them:</p>
        <div class="d-flex">
            <a href="javascript:void(0);" class="detail-link">
                <img src="https://opencollective.com/static/images/opencollective-icon.svg" alt="" width="25px" height="25px">
                <img src="https://opencollective.com/static/images/logotype.svg" alt="" width="120px" height="auto">
            </a>
            <a href="javascript:void(0);" class="detail-link">
                Become A Patreon
            </a>
        </div>
        <div class="sponsor-des">
            <strong>What's the difference between Patreon and OpenCollective?</strong>
            <p>Sponsored by Patreon, goes directly to support Ran Luo's work on Mark Text. Sponsored by Open Collective, all expenses are transparent, and used for the development of Mark Text.</p>
            <strong>We also accept One Time Donations</strong>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Sponsor'
}
</script>

<style>
    a {
        display: block;
    }
    .list a img {
    width: 100%;
    height: 100%;
    }
    .sponsor {
        background-color:#FEF7F7 !important;
        position: relative;
        height: 100vh;
        width: 100%;
        position: relative;
        background-size: 250px 250px;
        background-position: 35% 90%;
        background-image: url('../assets/man3.svg');
        background-repeat: no-repeat;
        display: flex;
        padding: 150px 100px;
        overflow: hidden;
        padding-bottom: 15px;
        opacity: 1;
    }
    .sponsor h2.slogan {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 50px;
        font-size: 35px;
        text-transform: uppercase;
        font-weight: 100;
        color:rgba(34, 181, 111, 1);
        border-bottom: 1px dashed currentColor;
    }
    .sponsor-list {
        width: 50%;
        color: #666;
    }
    .sponsor-item .list {
        display: inline-block;
        height: auto;
        width: 120px;
        margin-right: 30px;
    }
    .sponsor-item h3, .sponsor-detail h3 {
        text-align: center;
        font-size: 30px;
        margin-bottom: 25px;
        font-weight: 100;
    }
    .sponsor-detail {
        width: 50%;
        color: #666;
    }
    .sponsor-detail p a {
        display: inline;
        color:rgba(34, 181, 111, 1);
        text-decoration: underline;
    }
    .sponsor-detail p {
        margin-bottom: 20px;
        margin-top: 30px;
        font-size: 16px;
    }
    @media screen and (max-width: 1063px){
      .sponsor-detail p {
        font-size: 13px;
      }
    }
    .detail-link {
        display: flex;
        align-items: center;
    }
    .detail-link img {
        margin: 0 4px;
    }
    .d-flex {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 5px 0;
        margin-bottom: 30px;
    }
    a.detail-link {
        color: #fff;
    }
    a.detail-link:first-of-type {
        border: 1px solid #666;
        border-radius: 9999px;
        background: #fff;
        padding: 6px 8px;
    }
    a.detail-link:last-of-type {
        border: 1px solid #f96854;
        background: #f96854;
        border-radius: 5px;
        padding: 6px 8px;
    }
    @media screen and (max-width: 950px){
        .sponsor {
            height: auto;
            padding: 20px;
            overflow: auto;
            flex-direction: column;
        }
        .sponsor h2.slogan {
            position: relative;
            left: auto;
            top: auto;
            transform: none;
            max-width: 200px;
            margin: 0 auto 30px;
            text-align: center;
        }
        .sponsor-list,.sponsor-detail {
            width: 100%;
        }
        .sponsor-item {
            margin-bottom: 25px;
        }
    }
</style>



