<template>
  <div class="title-nav">
    <nav class="nav">
        <h1>Marktext</h1>
        <div class="nav-link">
            <a href="#" v-scroll-to="'#features'">Features</a>
            <a href="#" v-scroll-to="'#themes'">Themes</a>
            <a href="#" v-scroll-to="'#sponsor'">Sponsors</a>
            <a href="https://github.com/kid1z" target="_blank">
                <!-- github -->
                <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24"><path d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"/></svg>
                <p>Github</p>
            </a>
        </div>
        <div class="nav-toggle" @click="isOpen">
            <div class="nav-bar">
                <span class="bar"></span>
            </div>
        </div>
    </nav>
    <div class="des">
        <div class="des-text">
            <h1>
            Simple and Elegant Markdown Editor
            </h1>
            <h1>
            Focused on speed and usability.
            </h1>
        </div>
        <div class="des-img">
            <img :src="require(`@/assets/women.svg`)" alt="">
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TitleBar',
  methods: {
    isOpen () {
      var navLink = document.querySelector('.nav-link')
      navLink.classList.toggle('active-nav')
    }
  }
}
</script>

<style>
a {
    text-decoration: none;
}
.active-nav {
    opacity: 1 !important;
    visibility: visible !important;
    height: 250px !important;
}
.title-nav {
    height: 100vh;
    width: 100%;
    background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
    background-size: 400% 400%;
    animation: gradientColor 15s ease infinite  ;
    overflow: hidden;
}
@keyframes gradientColor {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
nav.nav {
    display: flex;
    align-items: center;
    width: 100%;
    height: 60px;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, .5);
    justify-content: space-between;
    z-index: 10;
}
.nav h1 {
    margin-left: 30px;
    line-height: 25px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: 30px;
    font-weight: 900;
}
.nav-link {
    display: flex;
    align-items: center;
    margin-right: 30px;
    transition: .3s all ease-in-out;
    transition-delay: .05s;
}

.nav-toggle {
    display: none;
    padding: 5px;
    margin-right: 20px;
}
.nav-bar {
    position: relative;
    width: 33px;
    height: 20px;
    cursor: pointer;
}
.nav-toggle span {
    width: 100%;
    height: 3px;
    background: #000 !important;
    display: block;
    top: 0;
}
.nav-toggle span:before, .nav-toggle span:after {
    content: '';
    position: absolute;
    left: 0;
    height: 3px;
    width: 100%;
    background: #000;
}
.nav-toggle span:before {
    top: 45%;
}
.nav-toggle span:after {
    bottom: 0;
}
.nav-link a {
    padding: 5px;
    margin-right: 30px;
    color: #000;
    opacity: .75;
    font-size: 15px;
    font-weight: 900;
}
.nav-link a:last-of-type {
    display: flex;
    align-items: center;
}
.nav-link a:hover {
    color: rgba(33, 181, 111, 1)
}
.nav-link a:hover svg {
    fill:rgba(33, 181, 111, 1)
    /* fill: inherit; */
}
.nav-link a svg {
    margin-right: 5px;
}
.des {
    margin-top: 60px;
    display: flex;
}
.des-text {
    margin-top: 180px;
    padding: 50px 100px;
    width: 60vw;
    color: #fff;
    text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.48);
}
.des-img {
    padding-top: 150px;
    width: 40vw;
    flex: 1;
}
.des-img img {
    width: 450px;
    height: 450px;
    padding: 5px;
}
@media screen and (max-width: 950px) {
    .nav-link {
        /* display: none; */
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 60px;
        width: 100%;
        line-height: 50px;
        background: inherit;
        opacity: 0;
        visibility: hidden;
        height: 0;
    }
    .title-nav {
        height: 100% !important;
    }
    .nav-toggle {
        display: block;
    }
    .des {
        display: flex;
        flex-direction: column-reverse;
    }
    .des-text {
        margin-top: 0;
        padding: 10px 13px;
        width: 100%;
        font-size: 10px;
        margin-bottom: 10px;
        margin-top: 10px;
    }
    .des-img {
        width: 100%;
        max-width: 460px;
        margin: 0 auto;
        padding-top: 0;
    }
}
</style>



