<template>
  <div class="futures" id="features">
    <h2 class="slogan" > Features😳</h2>
    <div class="futures-list">
      <ul>
        <li class="futures-items"
          v-for="(item, index) in items"
          :key="index"
          :class='{ active : currentLi === index}'
          @click="changeIndexLi(index)"
        >
        <div class="item-title">{{item.title}}</div>
        <div class="item-description">{{item.description}}</div>
        </li>
      </ul>
    </div>
    <div class="futures-table" v-bind="renderListTable()">
      <!-- <div class="table-block" v-if="isOpen" > -->
      <div class="table-block" v-if="this.currentLi === 0" >
        <h3>Table Block</h3>
        <table>
          <thead>
            <tr>
              <th><strong>Inline Syntax</strong></th>
              <th>Syntax</th>
              <th>Preview</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td><strong>Strong</strong></td>
              <td>**Strong**</td>
              <td><strong>Strong</strong></td>
            </tr>
            <tr>
              <td><strong>Inline Code</strong></td>
              <td>`yarn add muya`</td>
              <td><code>yarn add muya</code></td>
            </tr>
            <tr>
              <td><strong>Inline Math</strong></td>
              <td>$a \ne b$</td>
              <td><mark>a != b</mark></td>
            </tr>
            <tr>
              <td><strong>Hyper Link</strong></td>
              <td>[Google]<a href="https://www.google.com" target="_blank">(http://google.com)</a></td>
              <td><a href="https://www.google.com" target="_blank">Google</a></td>
            </tr>
            <tr>
              <td><strong>Strike</strong></td>
              <td>~~Strike~~</td>
              <td><del>Strike</del></td>
            </tr>
            <tr>
              <td><strong>Raw Html</strong></td>
              <td>
                {{rawHtml1}}
                <br>
                <p v-text="rawHtml2"></p>
              </td>
              <td><span style="color: red">Red Color</span><br>
                  <ruby>北京<rt>Beijing</rt></ruby></td>
            </tr>
            <tr>
              <td><strong>Emphasis</strong></td>
              <td>*Emphasis*</td>
              <td><i>Emphasis</i></td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="diagram" v-else>
        <h3>Diagram</h3>
        <div class="diagram-text">
          <p><strong>Mark Text</strong> support all <a href="javascript:void(0);">CommonMark Spec</a> and <a href="javascript:void(0);">GitHub Flavored Markdown Spec</a> syntax.</p>
          <p><strong>Strong</strong>, <i>Emphasis</i>, <code>Inline code</code>, <del>Strike</del>, 😄 <a href="javascript:void(0);">Link</a></p>
        </div>
        <div class="diagram-img">
          <img src="../assets/img.jpg" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Futures',
  data () {
    return {
      rawHtml1: '<span style="color: red">Red Color</span>',
      rawHtml2: '<ruby>北京<rt>Beijing</rt></ruby>',
      currentLi: 0,
      isOpen: Boolean,
      items: [
        {'title': 'Table Block', 'description': 'Support GFM table block, you can remove/add rows and columns.'},
        {'title': 'Diagram', 'description': 'Support Flowchart, Sequence diagram, Gantt diagram, Vega chart.'},
        {'title': 'Inline Formats', 'description': 'Support CommonMark and GitHub Flavored Markdown Spec.'},
        {'title': 'Math Formula', 'description': 'Markdown extensions math expressions (KaTeX)'},
        {'title': 'Code Block', 'description': 'Support GFM code fense, highlight by prismjs.'}
      ]
    }
  },
  methods: {
    changeIndexLi (e) {
      this.currentLi = e
    },
    renderListTable () {
      if (this.currentLi === 0) {
        this.isOpen = true
      } else this.isOpen = false
    }
  }
}
</script>

<style>
  .futures {
      height: 100vh;
      width: 100%;
      background: #F9F9F9;
      position: relative;
      background-size: 400px 400px;
      background-position: 50% 100%;
      background-image: url('../assets/man.svg');
      background-repeat: no-repeat;
      display: flex;
      padding: 150px 100px;
      overflow: hidden;
      padding-bottom: 15px;
  }
  .active {
    background: linear-gradient(30deg, #efefef, rgba(245, 245, 245, .2));
    color: rgba(33, 181, 111, 1);
  }
  ul {
    list-style: none;
    color: #666;
    padding: 0 20px;
  }
  .futures h2.slogan {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 50px;
    font-size: 35px;
    text-transform: uppercase;
    font-weight: 100;
    color:rgba(34, 181, 111, 1);
    border-bottom: 1px dashed currentColor;
  }
  li.futures-items {
    padding: 8px 10px;
    margin-bottom: 20px;
    border-radius: 8px;
    cursor: pointer;
  }
  @media screen and (min-width: 1224px){
    .futures-items .item-title{
      font-size: 26px !important;
    }
  }
  @media screen and (max-width: 1223px){
    .futures-items .item-title{
      font-size: 19px !important;
    }
  }
  @media screen and (max-width: 1063px){
    .futures-items .item-title{
      font-size: 17px !important;
    }
  }
  @media screen and (min-width: 1224px){
    .futures-items .item-description{
      font-size: 18px !important;
    }
  }
  @media screen and (max-width: 1063px){
    li.futures-items .item-description {
      font-size: 11px !important;
    }
  }
  @media screen and(max-width: 970px){
  }
  .futures-table {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    width: 500px;
    height: auto;
    background:#fff;
    box-shadow: 0px 0px 12px 2px #b0b0b0;
    /* margin-right: 30px; */
    padding: 0 20px;
    color: #666;
    position: relative;
    font-size: 14px
  }
  @media screen and (min-width: 1224px){
    .futures-table table{
      font-size: 14px;
    }
  }
  @media screen and (max-width: 1223px){
    .futures-table table{
      font-size: 13px;
    }
  }
  @media screen and (max-width: 1063px){
    .futures-table table{
      font-size: 13px;
    }
  }

  .futures-table h3 {
    margin-bottom: 25px;
    margin-top: 25px;
    font-weight: normal;
    font-size: 25px;
  }
  table {
    border-collapse: collapse;
    width: 100%;
    text-align: center;
    font-size: 14px;
  }
  thead tr th {
    text-align: center;
    border: 1px solid rgba(0, 0, 0, .1);
    padding: 6px 13px;
  }
  thead tr th:nth-child(1) {
    text-align: left;
  }
  table thead tr, tr:nth-child(2n) {
    background-color: rgba(0, 0, 0, .03);
  }
  tbody tr td {
    border: 1px solid rgba(0, 0, 0, .1);
    padding: 6px 13px;
  }
  td a {
    color:rgba(33, 181, 111, 1);
    text-decoration: underline;
  }
  tbody tr td:nth-child(1) {
    text-align: left;
  }
  @media screen and (max-width: 950px) {
    .futures {
      height: auto;
      padding: 20px;
      flex-direction: column;
      overflow: auto;
      padding-bottom: 400px;
    }
    .futures h2.slogan {
      position: relative;
      left: auto;
      top: auto;
      transform: none;
      text-align: center;
      max-width: 230px;
      margin: 0 auto 20px;
    }
    .futures-items .item-title {
      font-size: 21px !important;
    }
    li.futures-items .item-description {
      font-size: 14px !important;
    }
    .futures-table {
      width: auto;
      padding: 20px;
    }
    .futures-table table {
      font-size: 13px;
    }
    .futures-table h3 {
      margin-top: 0;
    }
    .themes-option h2 {
      font-size: 20px;
    }
    .themes-option .inline {
      font-size: 13px !important;
    }
  }
    @media screen and (max-width: 320px) {
    .futures-table table {
        font-size: 10px;
      }
  }
  .diagram {
    margin-right: 20px;
    position: relative;
  }
  .diagram-img {
    width: auto;
    height: 300px;
  }
  .diagram-img img {
    width: 100%;
    height: 100%;
  }
  .diagram-text a {
    display: inline;
    text-decoration: underline;
    color:rgba(33, 181, 111, 1);
  }
</style>



